import React from "react";
import {Link} from "react-router-dom";
import "./Navigation.css";

function Navigation(){
    return <div className="nav">
        <div className="nav_item">
        <Link to="/">Home</Link>    
        </div>
        <div className="nav_item2" >
        <Link to={{
            pathname: "/about",
            state: {
                fromNavigation: true
            }
        }}>About
        </Link>    
        </div>
    </div>
}

export default Navigation;